button {
  cursor: pointer;
}
.border-top {
  border-top: 1px solid #e5e5e5;
}
.border-bottom {
  border-bottom: 1px solid #e5e5e5;
}
.box-shadow {
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);
}
.loading {
  pointer-events: none;
  position: relative;
  animation: loading-fader 1s ease-in 0s infinite alternate;
}
.loading:after {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  border: 5px solid rgba(0, 0, 0, 0);
  border-top-color: black;
  border-bottom-color: black;
  border-radius: 100%;
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
  transform: translate(-50%, -50%);
  z-index: 100;
  animation: loading-rotor 0.7s linear 0s infinite;
}
@keyframes loading-fader {
  from {
    opacity: 0.3;
  }
  to {
    opacity: 0.5;
  }
}
@keyframes loading-rotor {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
th[sort] {
  color: #0050a5;
  cursor: pointer;
}
th[sort]:hover {
  color: #0c82ff;
}
th[sort] i {
  margin-right: 5px;
}
th[sort] i.glyphicon-chevron-up {
  vertical-align: top;
}
th[sort] i.glyphicon-chevron-down {
  vertical-align: middle;
}
h1 {
  font-size: 1.5em;
  font-weight: 100;
}
.card-img-top {
  height: 250px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.img-thumbnail {
  width: 100px;
}

